<template>
  <div class="main">
    <div class="close">
      <i class="el-icon-close" @click="deletePic"/>
    </div>

    <el-row :gutter="10">
      <div v-for="(data, index) in obj.urls" :key="index">
        <el-col :span="4">
          <el-card :body-style="{ padding: '5px' }">
            <div class="image_main">
              <img v-bind:src="data" class="image">
            </div>

          </el-card>
        </el-col>
      </div>
    </el-row>

    <!--<div  class="image_main">-->
      <!--<div v-for="(data, index) in obj.urls" :key="index">-->
        <!--<img v-bind:src="data" class="image">-->

      <!--</div>-->
    <!--</div>-->

    <el-input style="margin-left:1rem" v-model="obj.description"></el-input>

  </div>
</template>

<script>

  export default {
    name: "PicDataMotify",
    props: {
      obj: Object,
    },
    mounted() {
      console.log(this.obj)
    },
    methods: {
      deletePic() {
        console.log('deletePic')
        this.$emit('click_base', this.obj);
      }
    }
  }
</script>

<style lang="stylus" scoped>


  .main
    display flex;
    width 100%

    background #bfcbd9
    flex-direction column

    .el-row
      width 100%
      height 90%

      padding 5px

    .image_main
      display: flex;
      height: 100%;

      flex-direction: row;
      align-items: center;
      justify-content: center;

    .image {
      width: 8rem
      height :8rem
    }

    .close
      flex 1
      align-self flex-end
      display flex
      justify-content flex-start
      padding 0.45rem

  div div img {
    cursor: pointer;
    transition: all 0.6s;
  }


  div i:hover {
    transform: scale(1.8);
  }

</style>



