<template>


  <div class="main" v-loading="loading">

    <H1>添加商品</H1>

    <div class="input_main">
      <el-input v-model="url" placeholder="请输入图片地址"/>
      <el-input v-model="text" placeholder="请输入图片描述" class="d_text"/>
    </div>
    <el-button @click="save_pic" style=" margin-top: 1rem">网络文件保存</el-button>

    <div class="input_main">
      <input type="file" id="file" ref="file" multiple="multiple" v-on:change="readAsDataURL">
      <el-input v-model="text1" placeholder="请输入图片描述" class="d_text"/>

    </div>


    <PicS_Render :objs="temp_pic_files" @click_base="temp_pic_update"/>


    <!--<el-progress :text-inside="true" :stroke-width="18" v-bind:percentage="local_progress" color="rgba(00, 00, 00, 0.7)"-->
    <!--style="width: 30%"/>-->

    <el-button @click="saveLocalFile2QiNiu" style=" margin-top: 1rem">本地文件保存</el-button>


    <el-button v-if="pic_datas.length>0" round style="margin-left: 70%" type="success" @click="doSendServer">已绑定数据上传
    </el-button>

    <div v-for="(val ,key,index ) in pic_datas" class="list_main" v-viewer>
      <PicData :obj="val" @click_base="pic_update"/>
    </div>
  </div>


</template>

<script>
  import PicData from "./PicData";
  import PicS_Render from "./PicS_Render";

  // var qiniu = require('qiniu')
  // var qiniujs = require('qiniu-js')

  const axios = require('axios');

  export default {
    name: "ManagerAdd",
    components: {PicS_Render, PicData},
    data() {
      return {
        pic_datas: [],
        temp_pic_files: [],
        url: 'http://hdpublic.getlove.cn/5c8fce90641ec410596f3988.',
        text: '测试文本',
        text1: '本地文件描述(不同产品不要写相同的)',
        local_progress: 0,
        loading: false
      }
    },
    mounted() {
      //获取用户当前信息
      let current = Bmob.User.current()
      console.log(current)
      if (!current) {
        this.$message('未登录');
        this.$router.push('login')
        return
      }
    },
    methods: {
      save_pic() {
        if (this.url.trim().length == 0 || this.text.trim().length == 0) {
          this.$message('请输入完整的数据');
          return
        }
        console.log(' save_pic ' + this.url + this.text)
        this.pic_datas.push({'urls': [this.url], text: this.text})
      },
      pic_update(obj) {
        console.log(obj)
        this.pic_datas.remove(obj)
      },
      temp_pic_update(obj) {
        console.log(obj)
        this.temp_pic_files.remove(obj)
        this.$refs.file.value = '';
        console.log(this.temp_pic_files)
      },


      readAsDataURL() {
        if (this.temp_pic_files.length >= 20) {
          this.$message('最多只能添加20张图片');
          return
        }
        console.log('--readAsDataURL--')
        let localFile = this.$refs.file.files[0];

        var reader = new FileReader();
        reader.readAsDataURL(localFile);


        var _this = this;
        reader.onload = function (e) {
          // _this.$refs.img.src = this.result
          var key1 = this.result;
          _this.temp_pic_files.push({'src_url': key1, 'file': localFile})

          console.log('size ' + _this.temp_pic_files.length)
        }

      },

      saveLocalFile2QiNiu() {

        //七牛不能用了 现在改成聚合

        // let token='f88e1dced3494be0999ec111542e6064';

        this.loading = true

        var fileData = new FormData();
        fileData.append("token", "f88e1dced3494be0999ec111542e6064");
        fileData.append("sync", "0");
        fileData.append("categories", "pasha");
        fileData.append("v", '2');
        for (let i = 0; i < this.temp_pic_files.length; i++) {
          fileData.append("file" + i, this.temp_pic_files[i].file);
        }

        axios.post('https://api.superbed.cn/upload', fileData)
          .then(response => {
            if (response.data.err == 0) {
              this.pic_datas.push({'urls': response.data.urls, text: this.text1})
              this.localoverGG()
            } else {
              this.$message(response.data)
            }

          })
          .catch(error => {

            this.$message(error);

          });

      },

      localoverGG() {
        console.log('local upload over ')
        this.loading = false
        this.temp_pic_files = []
        this.$refs.file.value = '';

      },
      doSendServer() {

        this.loading = true

        const queryArray = new Array();
        // 构造含有50个对象的数组
        for (var i = 0; i < this.pic_datas.length; i++) {
          var queryObj = Bmob.Query('Pic_Data');
          queryObj.set('url', this.pic_datas[i].urls.join(","));
          queryObj.set('description', this.pic_datas[i].text);
          queryArray.push(queryObj);
        }

        // 传入刚刚构造的数组
        Bmob.Query('Pic_Data').saveAll(queryArray).then(result => {
          console.log(result);
          this.loading = false
          this.$message('数据保存完毕');
          this.pic_datas.length = 0
        }).catch(err => {

          this.$message(err);
          this.loading = false
        });

      }
    }
  }
</script>

<style lang="stylus" scoped>

  .main
    display flex;
    height 100%
    flex-direction column
    background-color: #E9EEF3;
    align-items center

    .input_main
      display flex;
      width 80%
      padding 1rem

      align-items center

      .d_text
        margin-left: 1rem;

    .list_main
      width 80%
      margin-top 1rem

    .list_main1
      display flex
      width 90%
      background red
      flex-direction row
      justify-content: center
      margin-top 1rem


</style>



