<template>


  <div class="main">

    <div class="close">
      <i class="el-icon-close" @click="deletePic"/>
    </div>

    <el-row :gutter="10">
      <div v-for="(data, index) in obj.urls" :key="index">
        <el-col :span="4">
          <el-card :body-style="{ padding: '5px' }">
            <div class="image_main">
              <img v-bind:src="data" class="image">
            </div>

          </el-card>
        </el-col>
      </div>
    </el-row>
    <div class="content">
      <span>{{ obj.text}}</span>
    </div>


  </div>
</template>

<script>
  export default {
    name: "PicData",
    props: {
      obj: Object,
    },
    mounted() {
      console.log(this.obj)
    },
    methods: {
      deletePic() {
        console.log('deletePic')
        this.$emit('click_base', this.obj);
      }
    }
  }
</script>

<style lang="stylus" scoped>

  .el-card{
    margin-top: 10px;
  }

  .content {
    padding: 14px;
    display: flex;
    height: 10%;
    flex-direction: column;
    justify-content: center;

  }

  .main
    display flex;
    width 100%

    background #bfcbd9
    flex-direction column

    .el-row
      width 100%
      height 90%

      padding 5px

    .image_main
      display: flex;
      height: 100%;

      flex-direction: row;
      align-items: center;
      justify-content: center;


  .image {
    width: 8rem
    height :8rem
  }

  .close
    flex 1
    align-self flex-end
    display flex
    justify-content flex-start
    padding 0.45rem

  div div img {
    cursor: pointer;
    transition: all 0.6s;
  }


  div i:hover {
    transform: scale(1.8);
  }

</style>



